  required\n                  autofocus\n              />\n            </div>\n\n            <div class=\"mt-4\">\n              <JetLabel for=\"password\" value=\"Password\"/>\n              <JetInput\n                  id=\"password\"\n                  v-model=\"form.password\"\n                  type=\"password\"\n                  class=\"mt-1 block w-full\"\n                  required\n                  autocomplete=\"current-password\"\n              />\n            </div>\n\n            <div class=\"block mt-4\">\n              <label class=\"flex items-center\">\n                <JetCheckbox v-model:checked=\"form.remember\" name=\"remember\"/>\n                <span class=\"ml-2 text-sm text-gray-600\">Remember me</span>\n              </label>\n            </div>\n\n            <div class=\"flex items-center justify-end mt-4\">\n              <Link :href=\"route('password.request')\" class=\"underline text-sm text-gray-600 hover:text-gray-900\">\n                Forgot your password?\n              </Link>\n\n              <JetButton class=\"ml-4\" :class=\"{ 'opacity-25': form.processing }\" :disabled=\"form.processing\">\n                Log in\n              </JetButton>\n            </div>\n          </form>\n        </div>\n\n        <footer>\n          <div class=\"flex justify-between modal-footer\">\n            <button\n                @click=\"clearForm\"\n                class=\"bg-gray-300 p-2 rounded-md hover:bg-gray-400 hover:text-gray-800\"\n            >Cancel\n            </button>\n            <div class=\"mt-2\">\n              Need to\n              <button @click=\"showRegister\" class=\"text-blue-800 hover:text-blue-600\">register</button>\n              for an account?\n            </div>\n          </div>\n        </footer>\n      </div>\n    </div>\n  </Transition>\n</template>\n\n<script setup>\nimport { useWelcomeStore } from \"@/Stores/WelcomeStore\"\nimport { useForm } from '@inertiajs/inertia-vue3'\nimport JetAuthenticationCard from '@/Jetstream/AuthenticationCard'\nimport JetAuthenticationCardLogo from '@/Jetstream/AuthenticationCardLogo'\nimport JetButton from '@/Jetstream/Button'\nimport JetInput from '@/Jetstream/Input'\nimport JetCheckbox from '@/Jetstream/Checkbox'\nimport JetLabel from '@/Jetstream/Label'\nimport JetValidationErrors from '@/Jetstream/ValidationErrors'\n\nconst welcomeStore = useWelcomeStore()\n\ndefineProps({\n  canResetPassword: Boolean,\n  status: String,\n  show: Boolean\n});\n\nconst form = useForm({\n  email: '',\n  password: '',\n  remember: false,\n});\n\nconst submit = () => {\n  form.transform(data => ({\n    ...data,\n    remember: form.remember ? 'on' : '',\n  })).post(route('login'), {\n    onFinish: () => form.reset('password'),\n  });\n};\n\nfunction clearForm() {\n  form.reset();\n  welcomeStore.showLogin = false;\n}\n\nfunction showRegister() {\n  form.reset();\n  welcomeStore.showLogin = false;\n  welcomeStore.showRegister = true;\n}\n\n</script>\n\n<style scoped>\n.modal-mask {\n  position: fixed;\n  inset: 0;\n  background: rgba(0, 0, 0, 0.6);\n  display: grid;\n  place-items: center;\n  z-index: 100;\n}\n\n.modal-container {\n  background: white;\n  color: black;\n  padding: 1rem;\n  width: 80vw;\n  max-width: 500px;\n  border-radius: 8px;\n}\n\n.modal-footer {\n  border-top: 1px solid #ddd;\n  margin-top: 1rem;\n  padding-top: 0.5rem;\n  font-size: .8rem;\n}\n</style>\n"],"sourceRoot":""}]);
